import React from 'react';
import { NavLink } from 'react-router-dom'
import './header.css'
import falv from './falv.png';
import houtai from './houtai.png';



const Header = () => {
    return (
        <header>
            <nav>
                <img className='houtai' src={houtai}></img>
                <NavLink style={{marginLeft:'80px'}} activeClassName='active' exact to='/'>首页</NavLink>
                <NavLink activeClassName='active' to='/doc'>后台</NavLink>
                <NavLink activeClassName='active' to='/setup'>设置</NavLink>
            </nav>
        </header>
    )
}

const Setup = () => {
    return (
        <div>
            设置
        </div>
    )
}

const Login = (props) => {
    const login = ()=>{
        props.history.replace(props.location.state);
    }
    return (
        <div>
            <button onClick={login}>登录</button>
        </div>
    )
}

const Home = (props) => {
    // console.log(props);
    
    return (
        
        <div className='title' style={{textAlign:'center'}}>
            <img src={falv} alt='falv' style={{width:'160px',height:'140px',display:'inline-block',marginTop:'180px',marginLeft:'20px'}}/> 
            <p style={{position:'relative',top:'20px',fontSize:'40px'}}>法援宝后台管理系统</p>
        </div>
    )
}

export {Home};
export {Login};
export {Header};
export {Setup};
