import React, { Component } from 'react'
import {NavLink, Redirect,withRouter, Switch} from 'react-router-dom'
import { RouteWithSubRoutes } from '../App';
import {  Table } from 'antd';
import './doc.css';
import './core1.css';

const columns = [
    {
      title: '名字',
      dataIndex: 'name',
      // render: text => <a>{text}</a>,
    },
    {
      title: '居住地',
      dataIndex: 'homeland',
    },
    {
      title: '工作室',
      dataIndex: 'workspace',
    },
    {
      title:'操作',
      dataIndex:'operation'
    },
    {
      title:'你好'
    }
  ];
class Core1 extends  Component{
    constructor(){
        super();
        //声明状态
        this.state={
          lawyer:[]
        }
    }
    componentDidMount(){
    
    fetch('https://www.responsibility.pro:2347/lawyer')
    .then(res=>res.json())
    .then(res=>{
        console.log(res);
        this.setState({lawyer:res})
    })
  }
    render(){
      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      return (
          <div id="box">
               <Table id="customers"  columns={columns} dataSource={this.state.lawyer} />
          </div>
      );
  }

}
