const Doc = ({routes}) => {
    
    return (
        <div className='doc'>
            <div className='leftpage'>
              <ol>
                  <li style={{marginTop:'30px'}}><NavLink className='nlink' to='/doc/core1'>用户信息</NavLink></li>
                  <li><NavLink className='nlink' to='/doc/core2'>律师信息</NavLink></li>
                  <li><NavLink className='nlink' to='/doc/core3'>设&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;置</NavLink></li>
                  <li><NavLink className='nlink' to='/doc/core4'>消息信息</NavLink></li>
                  <li><NavLink className='nlink' to='/doc/core5'>咨询信息</NavLink></li>
              </ol>
            </div>
            <div className="content">
              <Switch>
                  {routes.map((route,i) => (
                      <RouteWithSubRoutes key={i} {...route} />
                  ))}
              </Switch>
              <Redirect from='/doc' to='/doc/core1' />
            </div>
            
        </div>
    )
}

export default withRouter(Doc);
export {Core1};
export {Core2};
export {Core3};
export {Core4};
export {Core5};